.imagen-temporal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6); /* Fondo oscuro semi-transparente */
  display: flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(10px); /* Borra el fondo */
  z-index: 1000; /* Asegúrate de que el modal esté encima de todo */
}

.imagen-temporal-content {
  position: relative;
  width: 80%; /* Ajusta según sea necesario */
  max-width: 500px; /* Controla el tamaño máximo de la imagen */
  display: flex;
  justify-content: center;
  align-items: center;
}

.imagen-temporal-image {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
  border-radius: 8px;
}

.imagen-temporal-progress-bar {
  position: absolute;
  top: 0;
  left: 0;
  height: 10px; /* Aumenta la altura para hacerla más grande */
  width: 0%; /* Comienza vacía para animarla */
  background: #4caf50;
  transition: width 0.1s linear;
  z-index: 3; /* La barra se mantiene encima de la imagen */
  border-radius: 5px; /* Esto suaviza las esquinas sin redondearlas por completo */
}